import { useNavigate } from 'react-router-dom';
import { AppRoot, Card, Title, Text, Divider } from '@telegram-apps/telegram-ui';
import { assets } from './data/assets';

export const AssetList = () => {
  const navigate = useNavigate();

  return (
    <AppRoot>
      <div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
        <div style={{ padding: '16px', width: '100%', maxWidth: 530 }}>
          <Title level="2" style={{ textAlign: 'left', color: 'var(--tg-theme-text-color, #000)' }}>
              ({assets.length})
          </Title>

          {assets.map((asset) => {

            const rawProgress = asset.raised / asset.goal;
            const progress = asset.raised > 0 ? Math.max(1, Math.floor(rawProgress * 100)) : 0;
            return (
              <Card
                key={asset.id}
                onClick={() => navigate(`/asset/${asset.id}`)}
                style={{
                  marginTop: 16,
                  padding: 0,
                  overflow: 'hidden',
                  borderRadius: 16,
                  backgroundColor: 'var(--tg-theme-bg-color,rgb(237, 239, 242))',
                  cursor: 'pointer',
                }}
              >
                <img
                  src={asset.image}
                  alt={asset.name}
                  style={{ width: '100%', height: 350, objectFit: 'cover' }}
                />

                <div style={{ padding: '16px' }}>
                  <Text style={{ color: 'var(--tg-theme-text-color, #000)', fontWeight: 600, fontSize: 18 }}>
                    {asset.name}
                  </Text>

                  <Divider style={{ margin: '-1px' }} />

                  <Text style={{ color: 'var(--tg-theme-hint-color, #666)', fontSize: 14 }}>
                     {asset.minInvest.toLocaleString()} USDT   {asset.returnRate}
                  </Text>

                  <Divider style={{ margin: '0px 0' }} />

                  {/*  - */}
                  <div style={{
                    height: 7,
                    width: '100%',
                    backgroundColor: 'var(--tg-theme-secondary-bg-color, rgb(255, 255, 255))',
                    borderRadius: 8,
                    overflow: 'hidden',
                    marginTop: 8,
                  }}>
                    <div style={{
                      height: '100%',
                      width: `${progress}%`,
                      backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
                    }} />
                  </div>

                  <div style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    fontSize: 14,
                    marginTop: 4,
                    color: 'var(--tg-theme-text-color, #000)',
                  }}>
                    <span>{asset.raised.toLocaleString()} USDT ({progress}%)</span>
                    <span>{asset.goal.toLocaleString()} USDT</span>
                  </div>
                </div>
              </Card>
            );
          })}
        </div>
      </div>
    </AppRoot>
  );
};
