import { useParams, useNavigate } from 'react-router-dom';
import { AppRoot, Title, Text, Button, Card } from '@telegram-apps/telegram-ui';
import { assets } from './data/assets';

export const AssetDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const asset = assets.find((a) => a.id === id);

  if (!asset) {
    return (
      <AppRoot>
        <div style={{ padding: 16 }}>
          <Text>  </Text>
          <Button onClick={() => navigate(-1)}> </Button>
        </div>
      </AppRoot>
    );
  }

  const rawProgress = asset.raised / asset.goal;
  const progress = asset.raised > 0 ? Math.max(1, Math.floor(rawProgress * 100)) : 0;

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
          minHeight: '100vh',
          padding: 16,
        }}
      >
        <div style={{ width: '100%', maxWidth: 600 }}>
          {/*  */}
          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
             
          </Button>

          {/*  */}
          <img
            src={asset.image}
            alt={asset.name}
            style={{
              width: '100%',
              borderRadius: 16,
              objectFit: 'cover',
              marginBottom: 16,
            }}
          />

          {/*  +  */}
          <Title level="2" style={{ marginBottom: 4, color: 'var(--tg-theme-text-color, #000)' }}>
            {asset.name}
          </Title>

          <Text style={{ color: 'var(--tg-theme-hint-color, #666)', marginBottom: 12 }}>
            {asset.subtitle}
          </Text>

          {/*  */}
          <div
            style={{
              height: 10,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
              borderRadius: 8,
              overflow: 'hidden',
              marginBottom: 8,
            }}
          >
            <div
              style={{
                height: '100%',
                width: `${progress}%`,
                backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
              }}
            />
          </div>

          <Text style={{ fontSize: 14, marginBottom: 16, color: 'var(--tg-theme-text-color, #000)' }}>
            {asset.raised.toLocaleString()} USDT ({progress}%)  {asset.goal.toLocaleString()} USDT
          </Text>

          {/*   */}
          <Button size="l" style={{ width: '100%', marginBottom: 24 }} onClick={() => navigate(`/asset/${id}/buy`)}>
             
          </Button>

          {/*  */}
          <Card
            style={{
              padding: 16,
              marginBottom: 24,
              marginRight: 8,
              width: '90%',
              maxWidth: 570,
              backgroundColor: 'var(--tg-theme-bg-color, #fff)',
              borderRadius: 16,
            }}
          >
            <Row
              label=" :"
              value={asset.returnRate ?? ''}
              hint="     "
            />
            <Row 
              label=" :" 
              value={asset.lifetime ?? ''} 
              hint="         "
              />
            <Row label=" :" value="1 USDT" />
            <Row
              label="  :"
              value={asset.saleStartDate ?? ''}
              last //    
            />
          </Card>

          {/*  */}
          <Title level="3" style={{ marginBottom: 8, color: 'var(--tg-theme-text-color, #000)' }}>
             
          </Title>
          <div style={{ marginBottom: 24 }}>
          <Text style={{ color: 'var(--tg-theme-text-color, #000)', whiteSpace: 'pre-line', }}>
            {asset.description}
          </Text>
          </div>

          {/*  */}
          <div onClick={() => navigate(`/asset/${id}/owners`)}
          style={{
            width: '94%',
            maxWidth: 570,
            marginBottom: 12,
            padding: '12px 16px',
            borderRadius: 8,
            backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
            color: 'var(--tg-theme-text-color, #000)',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            cursor: 'pointer',
            fontWeight: 600,
          }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}></Text>
            <Text style={{ fontSize: 20 }}></Text>
          </div>

          {/*  */}
          <div onClick={() => navigate(`/asset/${id}/documents`)}
          style={{
            width: '94%',
            maxWidth: 570,
            marginBottom: 12,
            padding: '12px 16px',
            borderRadius: 8,
            backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
            color: 'var(--tg-theme-text-color, #000)',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            cursor: 'pointer',
            fontWeight: 600,
          }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}></Text>
            <Text style={{ fontSize: 20 }}></Text>
          </div>

          {/*   */}
          <Button size="l" style={{ width: '100%', marginBottom: 32 }} onClick={() => navigate(`/asset/${id}/buy`)}>
             
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};

const Row = ({
  label,
  value,
  hint,
  last = false,
}: {
  label: string;
  value: string | number;
  hint?: string;
  last?: boolean;
}) => (
  <div
    style={{
      padding: '16px 0',
      borderBottom: last ? 'none' : '1px solid var(--tg-theme-secondary-bg-color, #eee)',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: hint ? 'flex-start' : 'center',
      gap: 16,
    }}
  >
    {/*   */}
    <div style={{ flex: 1 }}>
      <div>
        <Text
          style={{
            color: 'var(--tg-theme-text-color, #000)',
            fontWeight: 600,
            fontSize: 16,
            lineHeight: '20px',
          }}
        >
          {label}
        </Text>
      </div>
      {hint && (
        <div>
          <Text
            style={{
              color: 'var(--tg-theme-hint-color, #888)',
              fontSize: 13,
              lineHeight: '18px',
              marginTop: 4,
            }}
          >
            {hint}
          </Text>
        </div>
      )}
    </div>

    {/*   */}
    <div
      style={{
        textAlign: 'right',
        color: 'var(--tg-theme-text-color, #000)',
        fontWeight: 600,
        fontSize: 16,
        lineHeight: '20px',
        minWidth: 100,
        display: 'flex',
        marginRight: 10,
        alignItems: 'center',
      }}
    >
      {value}
    </div>
  </div>
);
